// 无授权码体验页面
<template>
  <div class="toVote">
    <h1 class="topicname">{{vote.title}}</h1>
    <div class="topicinfo">
      发起人：
      <span class="sponsor">admin</span>
      截止日期：
      <span class="deadline">{{vote.end_time}}</span>
    </div>
    <div class="attention">
      <h3>注意事项</h3>
      <ul>
        <li>1. 请为您的选择报以负责的态度</li>
      </ul>
    </div>
    <el-divider></el-divider>
    <div class="topicdesc">
      <h3>投票主题描述：</h3>
      <p>投就完了~</p>
      
       <h3>主题选项：</h3>
    </div>
    <div class="form">
      <el-card class="box-card" v-for="(option, index) in options" :key="`option-${index}`">
        <!-- 选项头像 -->
        <el-avatar
          :size="96"
          v-if="option.pic != '' && option.pic != null"
          :src="option.pic"
        ></el-avatar>
        <el-avatar :size="96" v-else>
          {{ option.content.substring(0, 1) }}
        </el-avatar>
        <!-- 选项名称 -->
        <h2>{{ option.content }}</h2>
        <span class="desc">{{ option.count != -1 ? `${option.count} 票` : '' }}</span>
        <el-checkbox
          v-model="result[index]"
          :true-label="1"
          :false-label="0"
          :disabled="!chooseAvailable"
          border
        >
          我要选TA
        </el-checkbox>
      </el-card>
    </div>
    <div class="submit">
      <el-button type="primary" @click="confirmDialogVisible = true" :disabled="!chooseAvailable">
        点击提交
      </el-button>
      <el-dialog
        title="提示"
        v-if="confirmDialogVisible"
        :visible.sync="confirmDialogVisible"
        width="30%"
        center
        top="19vh"
        :before-close="handleClose"
      >
        <p>是否确定提交您的选票？</p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="confirmDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="confirmSubmit">
            确 定
          </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Example',
  data() {
    return {
      options: [],
      vote: {},
      result: [],
      confirmDialogVisible: false,
      chooseAvailable: true
    }
  },
  methods: {
    handleClose(done) {
      done()
    },
    async showVoteResult() {
      const res = await axios.get(`/vote/collectOneVote/${this.vote.vote_id}`)
      const data = res.data.data.optionsStatistic
      for (const key in data) {
        for (let i=0; i<this.options.length; i++) {
          if (this.options[i].content === key) {
            this.options[i].count = data[key]
            break
          }
        }
      }
    },
    async confirmSubmit() {
      // // 发送数据到服务器
      this.result.filter((value, index) => {
        const vid = this.vote.vote_id
        const oid = this.options[index].option_id
        axios.post("/vote", {
          o_id: oid,
          v_id: vid
        })
      })

      // 提示客户端成功信息
      this.$message({
        message: '您已提交选票',
        type: 'success',
      })
      
      // 显示票数
      this.showVoteResult()

      // 隐藏弹框
      this.confirmDialogVisible = false
      this.chooseAvailable = false
      localStorage.setItem('voted', 'true')
    },
  },
  async mounted() {
    const res = await axios.get("/vote/getDetailedVoteById?vid=1")
    this.options = res.data.data.options.map(v => {
      v.count = -1
      return v
    })
    this.result = new Array(this.options.length)
    this.vote  = res.data.data.vote
    const v = localStorage.getItem('voted')
    if (v && v == 'true') {
      this.chooseAvailable = false
    }
    if (!this.chooseAvailable) {
      this.showVoteResult()
    }
  }
}
</script>

